.root{
    position: relative;
}

.input{
    border: 1px solid rgba(0,0,0,0.1);
    padding:15px 20px;
    padding-left: 45px;
    width: 320px;
    border-radius: 10px;
    font-size: 16px;
    transition: all 0.15s ease-in-out;

    // &:focus {
    //     border: 1px solid rgba(0,0,0,0.2);
    //     width: 400px;
    // }
}
.icon {
    width: 28px;
    height: 28px;
    opacity: 0.5;
    position: absolute;
    left: 12px;
    top: 14px;
}

.clearIcon {
    width: 18px;
    height: 18px;
    opacity: 0.3;
    position: absolute;
    right: 15px;
    top: 17px;
    cursor: pointer;
    
    &:hover {
        opacity: 0.8;
    }
}
